import React, { Component } from 'react'
import Board from '../Board/Board'
import PropTypes from 'prop-types'
import Stats from '../Stats/Stats'
import Header from '../Header/Header'
import { LEVELS, findLevelById } from '../constants/levels'
import { reconcileReward } from '../Stats/Stats'
import './Game.css'
import '../App.css'

const initialState = {
  startTime: null,
  moves: 0,
  money: 0,
  level: 1,
  lives: 4,
  stats: false,
  elapsedTime: 0.0,
  moneyEarned: 0,
  powerUps: []
}

class Game extends Component {
  constructor(props) {
    super(props)

    this.state = initialState
  }

  setTimer() {
    this.setState({
      startTime: new Date()
    })
  }

  doneShopping() {
    const { transition } = this.props
    const { level, lives, money } = this.state
    const nextLevel = level + 1

    if (nextLevel <= LEVELS.length) {
      transition(callback => {
        this.setState({
          ...initialState,
          lives,
          money,
          level: nextLevel
        }, callback())
      })
    }
  }

  resetGame() { this.setState(initialState) }

  startNewLevel() {
    const { moves, elapsedTime, moneyEarned } = initialState

    this.setState({ moves, elapsedTime, moneyEarned })
  }

  resetLevel() {
    const { transition } = this.props
    const { lives } = this.state

    transition(callback => {
      this.setState({
        lives: lives - 1,
      }, () => {
        const { lives } = this.state

        if (lives <= -1) {
          this.resetGame()
        }
      }, callback())
    })
  }

  updateMoves() {
    const { moves } = this.state
    this.setState({ moves: moves + 1 })
  }

  nextLevel() {
    const { transition } = this.props
    const { startTime, level, moves, money } = this.state
    const endTime = new Date()
    const elapsedTime = (endTime - startTime) / 1000
    const foundLevel = findLevelById(level)
    const { reward } = foundLevel
    const moneyEarned = reconcileReward(reward, elapsedTime, moves)

    transition(callback => {
      this.setState({
        elapsedTime,
        moneyEarned,
        money: money + moneyEarned,
        stats: true
      }, callback())
    })
  }

  render() {
    const { level, stats, moves, money, lives, elapsedTime, moneyEarned } = this.state
    const { transitioning, fadingOut } = this.props

    if (!level) { return <div /> }

    if (transitioning && !stats) { return <div /> }

    let className = 'game-container'
    if (fadingOut) { className += ' fade-out' }

    return(
      <div className='window-container'>
        <Header level={level} />

        <div className={className}>

          {stats ? (
            <Stats
              elapsedTime={elapsedTime}
              moneyEarned={moneyEarned}
              moves={moves}
              level={level}
              doneShopping={() => this.doneShopping()}
              fadingOut={fadingOut}
              money={money}
              updateMoney={() => this.updateMoney()}
              level={level}
            />
          ) : (
            <Board
              level={level}
              nextLevel={moves => this.nextLevel(moves)}
              lives={lives}
              restartLevel={() => this.resetLevel()}
              transitioning={transitioning}
              fadingOut={fadingOut}
              stats={stats}
              updateMoves={() => this.updateMoves()}
              setTimer={() => this.setTimer()}
            />
          )}
        </div>
      </div>
    )
  }
}

export default Game

Game.propTypes = {
  stats: PropTypes.bool,
  transitioning: PropTypes.bool,
  fadingOut: PropTypes.bool,
  transition: PropTypes.func,
}



