import { spacesToMove } from './playerControls'
import { boxPositions } from '../board/boxPositions'

export const updateBoxes = (level, newPositions, boxes, direction, difficulty) =>
  boxPositions(level).map(boxPos => {
    const box = boxes.find(obs => obs.position === boxPos)

    if (box.position === newPositions[0]) {
      return {
        ...box,
        position: boxPos + spacesToMove(direction, difficulty)
      }
    }
    return box
  })



