import React, { Component } from 'react'
import './App.css'
import Game from './Game/Game'

class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      transitioning: false,
      fadingOut: false,
    }
  }

  transition(callback) {
    return this.setState({
      fadingOut: true
    }, () => {
      setTimeout(() => {
        this.setState({
          fadingOut: false,
          transitioning: true
        }, () => {
          callback(() => {
            this.setState({
              transitioning: false,
              fadingOut: false
            })
          })
        })
      }, 500)
    })
  }

  render() {
    const { transitioning, fadingOut } = this.state
    return (
      <Game
        transition={(callback) => this.transition(callback)}
        transitioning={transitioning}
        fadingOut={fadingOut}
      />
    )
  }
}

export default App



