import React from 'react'
import { POWER_UPS } from '../constants/powerUps'
import './Stats.css'

export const reconcileReward = (reward, elapsedTime, moves) => {
  const timePenalty = elapsedTime
  const movePenalty = moves * 10

  const newReward = parseInt(reward - timePenalty - movePenalty, 10)

  if (newReward < 0) { return 0 }

  return newReward
}

const PowerUpInfo = ({ powerUp }) =>
  <div>
    <h2 className='shop-selection'>{powerUp.name}</h2>
    Cost: {powerUp.cost} <br />
    <p>{powerUp.description}</p>
  </div>

const Stats = ({ elapsedTime, moves, moneyEarned, doneShopping, fadingOut, money, level }) => {
  let className = 'stats'

  if (fadingOut) {
    className += ' fade-out'
    return <div />
  }

  return(
    <div className={className}>
      <div>
        <h1>Level {level} Complete</h1>
        <div className='stats-content'>
          Time: {elapsedTime} seconds<br />
          Moves: {moves} <br />
          Money Earned: {moneyEarned}<br />
        </div>
      </div>
      <div>
        <h2>Shop:</h2>
        <div className='stats-content'>
        Available Funds: {money}
          {POWER_UPS.map((powerUp, index) =>
            <PowerUpInfo powerUp={powerUp} key={index} />
          )}
        </div>
      </div>
      <div className='done-shopping' onClick={doneShopping}>next level</div>
    </div>
  )
}

export default Stats



